<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <div class="content">
        <div v-for="(item,index) of cyyList" :key="index" class="cyy-list">
          <van-swipe-cell>
            <div class="cyy-content" @click="xzCyy(item)">
              <van-field :value="item" readonly />
            </div>
            <template #right>
              <div class="sideslip">
                <div class="sideslip-item">
                  <div class="icon" @click="editCyy(item,index)"> <i class="iconfont iconxieruqianming" /> </div>
                </div>
                <div class="sideslip-item red">
                  <div class="icon" @click="delCyy(index)"> <i class="iconfont iconshanchu1" /> </div>
                </div>
              </div>
            </template>
          </van-swipe-cell>
        </div>
      </div>
      <van-dialog
        v-model="addShow"
        title="添加常用语"
        show-cancel-button
        @cancel="cyy=''"
        @confirm="addCyy()"
      >

        <div class="add-cyy-content">
          <van-field v-model="cyy" rows="1" type="textarea" autosize placeholder="请输入常用语" />
        </div>

      </van-dialog>
      <van-dialog
        v-model="editShow"
        title="修改常用语"
        show-cancel-button
        @confirm="saveCyy()"
      >
        <div class="add-cyy-content">
          <van-field v-model="cyy1" rows="1" type="textarea" autosize placeholder="请输入常用语" />
        </div>
      </van-dialog>
      <div class="common-space" />
      <div v-if="pageStyles !== 0">
        <error-page
          :page-style="pageStyles"
          :show-button="false"
        />
      </div>
      <!-- 底部按钮 begin -->
      <div class="foot-btn fixed">
        <ul>
          <li><van-button type="info" round size="large" @click="addShow=true">添加常用语</van-button></li>
        </ul>
      </div>
    <!-- 底部按钮 end -->
    </div>

  </div>

</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar'
import { pageRouter } from '@/common/js/mixin.js'
import errorPage from '@/components/result-page/index'
export default {
  name: 'XzrzCyy',
  components: {
    headerBar,
    errorPage
  },
  mixins: [pageRouter],
  data() {
    return {
      pageStyles: 0,
      editShow: false,
      cyy: '',
      cyy1: '',
      headerData: {
        title: '选择常用语',
        leftUrl: 'ShGzrz',
        leftText: ' '
      },
      cyyList: [],
      addShow: false,
      xzindex: ''
    }
  },
  watch: {
    cyyList() {
      if (this.cyyList.length === 0) {
        this.pageStyles = 1
      } else {
        this.pageStyles = 0
      }
    }
  },
  created() {
    this.cyyList = window.localStorage.getItem('cyyList') ? JSON.parse(window.localStorage.getItem('cyyList')) : []
  },
  activated() {
    this.cyyList = window.localStorage.getItem('cyyList') ? JSON.parse(window.localStorage.getItem('cyyList')) : []
  },
  methods: {
    delCyy(index) {
      this.cyyList.splice(index, 1)
      window.localStorage.setItem('cyyList', JSON.stringify(this.cyyList))
    },
    saveCyy() {
      if (!this.cyy1) {
        this.$toast('常用语不能为空！')
        return
      }
      this.cyyList[this.xzindex] = this.cyy1
      this.cyyList.splice(0, 1, this.cyyList[0])
      window.localStorage.setItem('cyyList', JSON.stringify(this.cyyList))
    },
    editCyy(cyy, index) {
      this.xzindex = index
      this.cyy1 = cyy
      this.editShow = true
    },
    addCyy() {
      if (!this.cyy) {
        this.$toast('常用语不能为空！')
        return
      }
      this.cyyList.push(this.cyy)
      this.cyy = ''
      window.localStorage.setItem('cyyList', JSON.stringify(this.cyyList))
    },
    xzCyy(cyy) {
      this.$router.push({
        name: 'ShGzrz',
        params: {
          cyy: cyy
        }
      })
    }
  }
}
</script>

<style lang="css" scoped>

</style>
